<template>
    <v-layout>
        <v-flex sm6 offset-sm3>
            <h1>{{ article.title }}</h1>
            <p>{{ article.text }}</p>
        </v-flex>
    </v-layout> 
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
    name: 'Article',
    data() {
        return {
            article: {}
        }
    },
    methods: {
        ...mapActions(['fetchArticles'])
    },
    computed: {
        ...mapGetters(['getArticle'])
    },
    created() {
        this.fetchArticles().then(articles => {
            this.article = this.getArticle(this.$route.params.id)
        });
    }
}
</script>

