<template>
    <v-toolbar app class="purple darken-2 white--text">
      <v-toolbar-side-icon 
        class="white--text"
        @click.stop="openDrawer">
      </v-toolbar-side-icon>
      <router-link to="/" class="header-btn-home ml-4">
        <v-toolbar-title>
          Home
        </v-toolbar-title>
      </router-link>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only">
        <v-btn
          :to="'/blog'"
          flat 
          class="white--text">
          Blog
        </v-btn>
        <v-btn
          :to="'/about'"
          flat 
          class="white--text">
          About
        </v-btn>
        <v-btn
          :to="'/contacts'"
          flat 
          class="white--text">
          Contacts
        </v-btn>
        <v-btn
          v-if="!isLoggedIn"
          :to="'/login'"
          flat 
          class="white--text">
          Login
        </v-btn>
        <v-btn
          v-if="!isLoggedIn"
          :to="'/register'"
          flat 
          class="white--text">
          Register
        </v-btn>
        <v-btn v-if="isLoggedIn" @click="logoutUser">
          Logout
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
</template>

<script>
import firebase from 'firebase/app';
import 'firebase/auth';

export default {
  name: 'Toolbar',
  data() {
    return {
      isLoggedIn: false,
      currentUser: false,
    }
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    openDrawer() {
      this.$emit('drawerOpened', true)
    },
    logoutUser() {
      let _this = this;
      firebase.auth().signOut().then(function() {
        _this.$router.go({ path: _this.$router.path });
      }).catch(function(error) {
        console.log(error);
      });
    }
  }
}
</script>

<style scoped>
    .header-btn-home {
        color: #fff;
        text-decoration: none;
    }
</style>
