<template>
    <v-layout>
        <v-flex 
            v-for="article in allArticles"
            :key="article.id"
            class="set-articles-grid">
            <v-card>
                <v-img
                src="https://cdn.vuetifyjs.com/images/cards/desert.jpg"
                aspect-ratio="2.75"
                ></v-img>
                <v-card-title primary-title>
                    <div>
                        <h1 class="headline mb-0">{{article.title}}</h1>
                        <div> {{article.text}} </div>
                    </div>
                </v-card-title>
                <v-card-actions>
                    <v-btn flat color="orange" :to="viewArticle(article.id)">Read more</v-btn>
                </v-card-actions>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
    name: 'Blog',
    methods: {
        ...mapActions(['fetchArticles']),
        viewArticle(articleID) {
            return `/article/${articleID}`;
        }
    },
    computed: {
        ...mapGetters(['allArticles'])
    },
    created() {
        this.fetchArticles();
    }
}
</script>

<style>
    .set-articles-grid {
        display: grid;
        grid-template-columns: auto auto auto;
        grid-gap: 10px;
    }
</style>
