<template>
  <v-app>
    <Toolbar @drawerOpened="openDrawer"/>
    <v-content>
      <v-container fluid>
        <router-view></router-view>
      </v-container>
    </v-content>
    <NavigationDrawer :drawer="drawer" @drawerClosed="closeDrawer"/>
    <Footer/>
  </v-app>
</template>

<script>
import Toolbar from './components/Toolbar';
import NavigationDrawer from './components/NavigationDrawer';
import Footer from './components/Footer';

export default {
  name: 'App',
  components: {
    'NavigationDrawer': NavigationDrawer,
    'Toolbar': Toolbar,
    'Footer': Footer
  },
  data() {
    return {
      drawer: false
    }
  },
  methods: {
    openDrawer(drawerOpened) {
      this.drawer = drawerOpened;
    },
    closeDrawer(drawerOpened) {
      this.drawer = !drawerOpened;
    }
  }
}
</script>

