<template>
     <v-layout>
        <v-flex xs12 sm6 offset-sm3>
            <v-form
                ref="form"
                v-model="valid"
                lazy-validation>
                <v-text-field
                    v-model="article.title"
                    label="Title"
                    required
                ></v-text-field>

                <v-textarea
                    v-model="article.text"
                    label="Text"
                    auto-grow
                    required>
                </v-textarea>
                <v-flex xs12>
                    <v-btn
                        :disabled="!valid"
                        color="success"
                        @click="editArticle"
                        >
                        Edit
                    </v-btn>

                    <!-- Dialog -->

                    <v-btn
                        color="error"
                        dark
                        @click.stop="dialog = true"
                        >
                        Delete
                    </v-btn>

                    <v-dialog
                        v-model="dialog"
                        max-width="290"
                    >
                        <v-card>
                            <v-card-title class="headline">Delete article</v-card-title>

                            <v-card-text>
                                Are you sure you want to delete this article?
                            </v-card-text>

                            <v-card-actions>
                                <v-spacer></v-spacer>

                                <v-btn
                                    color="green darken-1"
                                    flat="flat"
                                    @click="dialog = false"
                                >
                                    No
                                </v-btn>

                                <v-btn
                                    color="green darken-1"
                                    flat="flat"
                                    @click="delArticle"
                                >
                                    Yes
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-flex>
            </v-form>
        </v-flex>
     </v-layout>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
    name: 'EditArticle',
    data() {
        return {
            article: {},
            valid: true,
            dialog: false
        }
    },
    methods: {
        ...mapActions(['fetchArticles', 'updateArticle', 'deleteArticle']),
        editArticle() {
            if (this.$refs.form.validate()) {
                this.updateArticle(this.article).then(article => {
                    this.article = article;
                    this.$router.push('/dashboard');
                });
            }
        },
        delArticle() {
            this.deleteArticle(this.article).then(() => {
                this.$router.push('/dashboard');
            });
        }
    },
    computed: {
        ...mapGetters(['getArticle'])
    },
    created() {
        this.fetchArticles().then(articles => {
            this.article = this.getArticle(this.$route.params.id)
        });
    }
}
</script>

