import db from './../../firestoreInit';

const state = {
    articles: []
};

const getters = {
    allArticles: state => state.articles,
    getArticle: (state) => (articleID) => {
        return state.articles.find(article => article.id === articleID)
    }
}

const actions = {
    async fetchArticles({ commit }) {
        const response = await db.collection('articles').get().then(querySnapshot => {
            const docs = [];
            querySnapshot.forEach(doc => {
                const data = doc.data();
                const id = doc.id;
                docs.push({ id, ...data });
            })
            return docs;
        })
        commit('setArticles', response);
    },
    async addArticle({ commit }, article) {
        const response = await db
            .collection('articles')
            .add(article)
            .then(doc => {
                return doc;
            });
        commit('newArticle', response);
    },
    async updateArticle({ commit }, article) {
        const response = await db
            .collection('articles')
            .doc(article.id)
            .set(article)
            .then(() => {
                return article;
            });
        commit('updArticle', response);
    },
    async deleteArticle({ commit }, article) {
        const response = await db
            .collection('articles')
            .doc(article.id)
            .delete()
            .then(() => {
                return article.id;
            });
        commit('delArticle', response);
    } 
};

const mutations = {
    setArticles: (state, articles) => (state.articles = articles),
    newArticle: (state, newArticle) => {
        state.articles.push(newArticle);
    },
    updArticle: (state, updArticle) => {
        const index = state.articles.findIndex(article => article.id === updArticle.id);
        if (index !== -1) {
            state.articles.splice(index, 1, updArticle);
        }
    },
    delArticle: (state, deletedArticleID) => {
        (state.articles = state.articles.filter(article => article.id !== deletedArticleID));
    }
};

export default {
    state,
    getters,
    actions,
    mutations
}