<template>
    <v-navigation-drawer
      fixed
      temporary
      hide-overlay
      v-model="isDrawerOpen"
      app
      width="250"
    >
      <v-list>
        <v-list-tile @click.stop="closeDrawer">
          <v-list-tile-action>
            <v-icon>home</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Home</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <v-list-group
          prepend-icon="account_circle"
          value="true"
        >
          <template v-slot:activator>
            <v-list-tile>
              <v-list-tile-title>{{currentUser || 'Menu'}}</v-list-tile-title>
            </v-list-tile>
          </template>
          <v-list-group
            v-if="isAdmin"
            no-action
            sub-group
            value="true"
          >
            <template v-slot:activator>
              <v-list-tile>
                <v-list-tile-title>Admin</v-list-tile-title>
              </v-list-tile>
            </template>

            <v-list-tile
              v-for="(admin, i) in admins"
              :key="i"
              :to="admin[2]"
            >
              <v-list-tile-title v-text="admin[0]"></v-list-tile-title>
              <v-list-tile-action>
                <v-icon v-text="admin[1]"></v-icon>
              </v-list-tile-action>
            </v-list-tile>
          </v-list-group>

          <v-list-group
            sub-group
            no-action
          >
            <template v-slot:activator>
              <v-list-tile>
                <v-list-tile-title>Actions</v-list-tile-title>
              </v-list-tile>
            </template>
            <v-list-tile
              v-for="(crud, i) in cruds"
              :key="i"
            >
              <v-list-tile-title v-text="crud[0]"></v-list-tile-title>
              <v-list-tile-action>
                <v-icon v-text="crud[1]"></v-icon>
              </v-list-tile-action>
            </v-list-tile>
          </v-list-group>
        </v-list-group>
      </v-list>
    </v-navigation-drawer>
</template>

<script>
import firebase from 'firebase/app';
import 'firebase/auth';

export default {
  name: 'NavigationDrawer',
  props: ['drawer'],
  data() {
    return {
      isDrawerOpen: false,
      isLoggedIn: false,
      currentUser: false,
      isAdmin: false,
      admins: [
        ['Dashboard', 'settings', '/dashboard'],
        ['Manage', 'people_outline', '/manage']
      ],
      cruds: [
          ['Create', 'add'],
          ['Read', 'insert_drive_file'],
          ['Update', 'update'],
          ['Delete', 'delete']
      ]
    }
  },
  created() {
    const adminEmail = 'stamenov90@gmail.com';

    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
      this.isAdmin = (adminEmail === this.currentUser)
    }
  },
  methods: {
    closeDrawer() {
      this.isDrawerOpen = false;
      this.$router.push({ path: '/' })
    }
  },
  watch: { 
    drawer: function(newVal, oldVal) {
      this.isDrawerOpen = newVal
    },
    isDrawerOpen: function(newVal, oldVal) {
      if (!newVal) {
        this.$emit('drawerClosed', true)
      }
    }
  }
}
</script>

