<template>
    <v-layout>
        <v-flex xs12 sm4 offset-sm4>
            <ul
                class="list-articles">
                <li
                    v-for="article in allArticles" 
                    :key="article.id"
                    class="list-item-article pt-2 pl-2 pb-2 purple darken-2">
                    <router-link :to="editArticle(article.id)">
                        <a class="white--text">{{ article.title }}</a>
                    </router-link>
                </li>
            </ul>
        </v-flex>
        <v-flex xs12 sm3>
            <router-link to="/dashboard/add-article">
                <v-btn to="/dashboard/add-article" color="success" dark large>Add article</v-btn>
            </router-link>
        </v-flex>
    </v-layout>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
    name: 'Dashboard',
    methods: {
        ...mapActions(['fetchArticles']),
        editArticle(articleID) {
            return `/dashboard/${articleID}`;
        }
    },
    computed: {
        ...mapGetters(['allArticles'])
    },
    created() {
        this.fetchArticles();
    }
}
</script>

<style scoped>
.list-articles {
    list-style-type: none;
}

.list-item-article {

}
</style>
