<template>
     <v-layout>
        <v-flex xs12 sm6 offset-sm3>
            <v-form
                ref="form"
                v-model="valid"
                lazy-validation>
                <v-text-field
                    v-model="title"
                    :rules="titleRules"
                    label="Title"
                    required
                ></v-text-field>

                <v-textarea
                    v-model="text"
                    :rules="textRules"
                    label="Text"
                    auto-grow
                    required>
                </v-textarea>
                <v-flex xs12>
                    <v-btn
                        :disabled="!valid"
                        color="success"
                        @click="createArticle"
                        >
                        Add article
                    </v-btn>
                </v-flex>
            </v-form>
        </v-flex>
     </v-layout>
</template>

<script>
import { mapActions } from 'vuex';

export default {
    name: 'AddArticle',
    data() {
        return {
            title: '',
            text: '',
            valid: false,
            titleRules: [
                (v) => !!v || 'Title is required',
            ],
            textRules: [
                (v) => !!v || 'Text is required',
            ]
        }
    },
    methods: {
        ...mapActions(['addArticle']),
        createArticle() {
            let _that = this;
            if (this.$refs.form.validate()) {
                this.addArticle({ title: _that.title, text: _that.text }).then(() => {
                    this.$router.push('/dashboard');
                })
            }
        }
    }
}
</script>

